  width:100vw;
  height: 60px;
  background: -webkit-gradient(linear,left top, right top,from(whitesmoke),to(white));
  background: linear-gradient(to right,whitesmoke,white);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
