          align-items: center;
}
.footer_item{
  color: grey;
  font-size: 13px;
}
.scrolldiv{
  width: 100vw;
  height: 90vh;
  overflow-y: scroll;
}
.card{
  width: 640px;
  height: 600px;
  border-radius: 15px;
  border: solid 1.5px whitesmoke;
  background-color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-bottom: 20px;
}
.card:hover{
  border: solid 1.5px #61CBF4;
}



